html {
  // this makes 1rem = 10px
  // derived from 10px/16 px 
  // useful for responsive layouts
  font-size: 62.5%;

  // replace these with your choice 
  font-family: Arial, Helvetica, sans-serif;

}

body {
  font-size: 2rem;
}

